import iBadge from "../badge/index";

export default {
  data() {
    return {
      relations: {
        '../tabs/index': {
          type: 'parent'
        }
      },
      current: false,
      currentColor: '',
      scroll: false
    };
  },

  components: {
    iBadge
  },
  props: {
    key: {
      type: String,
      value: ''
    },
    title: {
      type: String,
      value: ''
    },
    dot: {
      type: Boolean,
      value: false
    },
    count: {
      type: Number,
      value: 0
    }
  },
  methods: {
    changeCurrent(current) {
      this.setData({
        current
      });
    },

    changeCurrentColor(currentColor) {
      this.setData({
        currentColor
      });
    },

    changeScroll(scroll) {
      this.setData({
        scroll
      });
    },

    handleClickItem() {
      const parent = this.getRelationNodes('../tabs/index')[0];
      parent.emitEvent(this.key);
    },

    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
